import React from "react"
import { graphql } from "gatsby"

import Layout from "../components/layout"
import SEO from "../components/seo"

import Property from "../Properties/Property/Property"

export const query = graphql`
  query Property($id: ID!) {
    wordpress {
      property(id: $id) {
        PropertyBasics {
          status
          bathrooms
          bedrooms
          price
          propertytype
          sqft
        }
        PropertyImages {
          mainimage {
            altText
            mediaItemUrl
          }
          gallery {
            altText
            mediaItemUrl
          }
        }
        PropertyDetailedInfo {
          buildingage
          description
          garage
          rooms
          otherfeatures {
            aircondition
            balcony
            beach
            bedding
            fieldGroupName
            heating
            icon
            internet
            microwave
            parking
            smokingallow
            terrace
            wifi
          }
        }
        PropertyLocations {
          fieldGroupName
        }
        PropertyAgent {
          agent {
            ... on WORDPRESS_Agent {
              id
              Agent {
                name
                telephone
                email
              }
              featuredImage {
                altText
                mediaItemUrl
              }
            }
          }
        }
        title
        seo {
          title
        }
      }
    }
  }
`

const property = ({ data }) => {
  return (
    <Layout>
      <SEO title={data.wordpress.property.seo.title} />
      <Property data={data} />
    </Layout>
  )
}

export default property
