import React from "react"
import { FormattedNumber } from "react-intl"

function jsUcfirst(string) {
  return string.charAt(0).toUpperCase() + string.slice(1)
}

const SeperaterInfo = ({ PropertyLocations, PropertyBasics }) => {
  return (
    <section className="spd-wrap">
      <div className="container">
        <div className="row">
          <div className="col-lg-12 col-md-12">
            <div className="slide-property-detail">
              <div className="slide-property-first">
                <div className="pr-price-into">
                  <h2>
                    <FormattedNumber
                      value={PropertyBasics.price}
                      style="currency"
                      currency="CAD"
                    />
                    <i>
                      {PropertyBasics.status === "rent" ? "/ monthly" : null}
                    </i>{" "}
                    <span className="prt-type rent">
                      For {jsUcfirst(PropertyBasics.status)}
                    </span>
                  </h2>
                  <span>
                    <i className="lni-map-marker"></i>{" "}
                    {PropertyLocations.fieldGroupName}
                  </span>
                </div>
              </div>

              <div className="slide-property-sec">
                <div className="pr-all-info">
                  <div className="pr-single-info">
                    <div className="share-opt-wrap">
                      <button
                        type="button"
                        className="btn-share"
                        data-toggle="dropdown"
                        aria-haspopup="true"
                        aria-expanded="false"
                        data-original-title="Share this"
                      >
                        <i className="lni-share"></i>
                      </button>
                      <div className="dropdown-menu animated flipInX">
                        <a href="#" className="cl-facebook">
                          <i className="lni-facebook"></i>
                        </a>
                        <a href="#" className="cl-twitter">
                          <i className="lni-twitter"></i>
                        </a>
                        <a href="#" className="cl-gplus">
                          <i className="lni-google-plus"></i>
                        </a>
                        <a href="#" className="cl-instagram">
                          <i className="lni-instagram"></i>
                        </a>
                      </div>
                    </div>
                  </div>

                  <div className="pr-single-info">
                    <a
                      href="JavaScript:Void(0);"
                      data-toggle="tooltip"
                      data-original-title="Get Print"
                    >
                      <i className="ti-printer"></i>
                    </a>
                  </div>

                  <div className="pr-single-info">
                    <a
                      href="JavaScript:Void(0);"
                      className="compare-button"
                      data-toggle="tooltip"
                      data-original-title="Compare"
                    >
                      <i className="ti-control-shuffle"></i>
                    </a>
                  </div>

                  <div className="pr-single-info">
                    <a
                      href="JavaScript:Void(0);"
                      className="like-bitt add-to-favorite"
                      data-toggle="tooltip"
                      data-original-title="Add To Favorites"
                    >
                      <i className="lni-heart-filled"></i>
                    </a>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </section>
  )
}

export default SeperaterInfo
