import React from "react"
import Sidebar from "./components/Sidebar"
import MortgageCalculator from "./components/MortgageCalculator"

const Details = ({
  PropertyBasics,
  PropertyDetailedInfo,
  PropertyLocations,
  PropertyAgent,
}) => {
  console.error("DATA,PROPERTY", PropertyDetailedInfo)
  return (
    <section className="gray">
      <div className="container">
        <div className="row">
          <div className="col-lg-8 col-md-12 col-sm-12">
            <div className="block-wrap">
              <div className="block-header">
                <h4 className="block-title">Property Info</h4>
              </div>

              <div className="block-body">
                <ul className="dw-proprty-info">
                  <li>
                    <strong>Bedrooms</strong>
                    {PropertyBasics.bedrooms}
                  </li>
                  <li>
                    <strong>Bathrooms</strong>
                    {PropertyBasics.bathrooms}
                  </li>
                  <li>
                    <strong>Garage</strong>
                    {PropertyDetailedInfo.garage}
                  </li>
                  <li>
                    <strong>Area</strong>
                    {PropertyBasics.sqft} sq ft
                  </li>
                  <li>
                    <strong>Type</strong>
                    {PropertyBasics.propertyType}
                  </li>
                  <li>
                    <strong>Price</strong>${PropertyBasics.price}
                  </li>
                  <li>
                    <strong>City</strong>
                    {PropertyLocations.fieldGroupName}
                  </li>
                  <li>
                    <strong>Build On</strong>
                    {2020 - PropertyDetailedInfo.buildingAge}
                  </li>
                </ul>
              </div>
            </div>

            <div className="block-wrap">
              <div className="block-header">
                <h4 className="block-title">Description</h4>
              </div>

              <div className="block-body">
                <p>{PropertyDetailedInfo.description}</p>
              </div>
            </div>

            <div className="block-wrap">
              <div className="block-header">
                <h4 className="block-title">Ameneties</h4>
              </div>

              <div className="block-body">
                <ul className="avl-features third">
                  {Object.keys(PropertyDetailedInfo.otherfeatures).map(
                    feature => {
                      return (
                        <li
                          className={
                            PropertyDetailedInfo.otherfeatures[feature]
                              ? "check"
                              : ""
                          }
                        >
                          {feature}
                        </li>
                      )
                    }
                  )}
                </ul>
              </div>
            </div>
            {PropertyBasics.status === "sale" ? <MortgageCalculator /> : null}
          </div>
          <Sidebar agent={PropertyAgent.agent[0]} />
        </div>
      </div>
    </section>
  )
}

export default Details
