const prod = process.env.NODE_ENV === "production" ? true : false;
const link = prod ? "" : "http://localhost:3000";

export const FETCH_USER_INFO_NEW = `${link}/api/v1/users/login`;
export const FETCH_USER_PROPERTIES_NEW = `${link}/api/v1/properties/user/`;

export const FETCH_HOME_FEATURED_PROPERTIES = `${link}/api/v1/properties?homeFeatured=true`;

//PROPERTY LIST LINKS
export const FETCH_PROPERTY_LIST = (pageNumber, propertyPerPage) =>
  `${link}/api/v1/properties?page=${pageNumber}&limit=${propertyPerPage}&sort=-expirationDay`;
export const FETCH_NUMBER_OF_PROPERTIES = `${link}/api/v1/properties/count`;

//PROPERTY PAGE LINKS
export const FETCH_PROPERTY_PAGE = (pId) =>
  `${link}/api/v1/properties/url/${pId}`;

//SIDEBAR PROPERTIES LINKS
export const FETCH_SIDEBAR = `${link}/api/v1/properties?sidebarFeatured=true`;
