import React, { useState, useEffect } from "react"
import Slider from "react-slick"
import "./styles/ImageSlider.scss"
import SliderInfo from "./SliderInfo"

const ImageSlider = props => {
  let mainImageStateInitial = `${props.PropertyImages.mainimage.mediaItemUrl}`

  const [mainImageState, setMainImageState] = useState(mainImageStateInitial)

  const handleGalleryChange = url => {
    setMainImageState(url)
  }

  const settings = {
    dots: false,
    infinite: true,
    speed: 500,
    slidesToShow: 6,
    slidesToScroll: 1,
  }

  return (
    <>
      <div className="col-lg-7 col-md-7 col-sm-12">
        <div className="item-slick">
          <img style={{ width: "100%" }} src={mainImageState} alt="Alt" />
        </div>
      </div>
      <SliderInfo {...props} />
      <div className="col-lg-12 col-md-12 col-sm-12">
        <div className="slick-for">
          <Slider {...settings}>
            <div
              className="item-slick"
              onClick={() => handleGalleryChange(mainImageStateInitial)}
            >
              <img src={mainImageStateInitial} alt="Alt" />
            </div>
            {props.PropertyImages.gallery.map((image, index) => {
              return (
                <div
                  key={index}
                  className="item-slick"
                  onClick={() => handleGalleryChange(`${image.mediaItemUrl}`)}
                >
                  <img src={`${image.mediaItemUrl}`} alt={index + "-image"} />
                </div>
              )
            })}
            {props.PropertyImages.gallery.length < 6
              ? props.PropertyImages.gallery.map((image, index) => {
                  return (
                    <div
                      key={index}
                      className="item-slick"
                      onClick={() =>
                        handleGalleryChange(`${image.mediaItemUrl}`)
                      }
                    >
                      <img
                        src={`${image.mediaItemUrl}`}
                        alt={index + "-image"}
                      />
                    </div>
                  )
                })
              : null}
          </Slider>
        </div>
      </div>
    </>
  )
}

export default ImageSlider
