import React from "react"
import { useStaticQuery, graphql } from "gatsby"
import { Link } from "gatsby"
import Slider from "react-slick"

const Sidebar = ({ agent }) => {
  const data = useStaticQuery(graphql`
    query SideBarProperties {
      wordpress {
        properties {
          nodes {
            PropertyOptions {
              sidebarfeatured
            }
            title
            slug
            PropertyBasics {
              status
              price
              status
              bedrooms
              bathrooms
            }
            PropertyImages {
              mainimage {
                sourceUrl
                altText
              }
              gallery {
                sourceUrl
                altText
              }
            }
            PropertyAgent {
              agent {
                ... on WORDPRESS_Agent {
                  id
                  Agent {
                    name
                  }
                  featuredImage {
                    mediaItemUrl
                  }
                }
              }
            }
            PropertyLocations {
              fieldGroupName
            }
          }
        }
      }
    }
  `)

  const settings = {
    dots: true,
    infinite: true,
    speed: 500,
    slidesToShow: 1,
    slidesToScroll: 1,
  }

  return (
    <div className="col-lg-4 col-md-12 col-sm-12">
      <div className="page-sidebar">
        <div className="agent-widget">
          <div className="agent-title">
            <div className="agent-photo">
              <img
                src={
                  agent
                    ? `${agent.featuredImage.mediaItemUrl}`
                    : `https://via.placeholder.com/150`
                }
                alt=""
              />
            </div>
            <div className="agent-details">
              <h4>
                <a href="#">{agent ? agent.Agent.name : "Unknown"}</a>
              </h4>
              <span>
                <i className="lni-phone-handset"></i>
                {agent ? agent.Agent.telephone : "Unknown"}{" "}
              </span>
            </div>
            <div className="clearfix"></div>
          </div>

          <div className="form-group">
            <input
              type="text"
              className="form-control"
              placeholder="Your Email"
            />
          </div>
          <div className="form-group">
            <input
              type="text"
              className="form-control"
              placeholder="Your Phone"
            />
          </div>
          <div className="form-group">
            <textarea className="form-control">
              I'm interested in this property.
            </textarea>
          </div>
          <button className="btn btn-theme full-width">Send Message</button>
        </div>

        <div className="sidebar-widgets">
          <h4>Featured Property</h4>

          <div className="sidebar-property-slide">
            <Slider {...settings}>
              {data.wordpress.properties.nodes
                .filter(node => {
                  if (node.PropertyOptions.sidebarfeatured) {
                    return true
                  } else {
                    return false
                  }
                })
                .map(property => {
                  return (
                    <div className="single-items">
                      <div className="property-listing property-1">
                        <div className="listing-img-wrapper">
                          <Link to={`/property/${property.slug}`}>
                            <img
                              src={`${property.PropertyImages.mainimage.sourceUrl}`}
                              className="img-fluid mx-auto"
                              alt=""
                            />
                          </Link>
                          <div className="listing-like-top">
                            <i className="ti-heart"></i>
                          </div>
                          <div className="listing-rating">
                            <i className="ti-star filled"></i>
                            <i className="ti-star filled"></i>
                            <i className="ti-star filled"></i>
                            <i className="ti-star filled"></i>
                            <i className="ti-star"></i>
                          </div>
                          <span className="property-type">
                            For {property.PropertyBasics.status}
                          </span>
                        </div>

                        <div className="listing-content">
                          <div className="listing-detail-wrapper">
                            <div className="listing-short-detail">
                              <h4 className="listing-name">
                                <Link to={`/property/${property.slug}`}>
                                  {property.title}
                                </Link>
                              </h4>
                              <span className="listing-location">
                                <i className="ti-location-pin"></i>
                                {property.PropertyLocations.fieldGroupName}
                              </span>
                            </div>
                            <div className="list-author">
                              <Link to={`/property/${property.slug}`}>
                                <img
                                  src={
                                    property.PropertyAgent.agent.length > 0
                                      ? `${property.PropertyAgent.agent[0].featuredImage.mediaItemUrl}`
                                      : `https://via.placeholder.com/150`
                                  }
                                  className="img-fluid img-circle avater-30"
                                  alt=""
                                />
                              </Link>
                            </div>
                          </div>

                          <div className="listing-features-info">
                            <ul>
                              <li>
                                <strong>Bed:</strong>
                                {property.PropertyBasics.bedrooms}
                              </li>
                              <li>
                                <strong>Bath:</strong>
                                {property.PropertyBasics.bathrooms}
                              </li>
                              <li>
                                <strong>Sqft:</strong>
                                {property.PropertyBasics.sqft}
                              </li>
                            </ul>
                          </div>

                          <div className="listing-footer-wrapper">
                            <div className="listing-price">
                              <h4 className="list-pr">
                                ${property.PropertyBasics.price}
                              </h4>
                            </div>
                            <div className="listing-detail-btn">
                              <Link
                                to={`/property/${property.slug}`}
                                className="more-btn"
                              >
                                More Info
                              </Link>
                            </div>
                          </div>
                        </div>
                      </div>
                    </div>
                  )
                })}
            </Slider>
          </div>
        </div>
      </div>
    </div>
  )
}

export default Sidebar
